
import { defineComponent } from "vue";
import { NotificationService } from "@/services/NotificationService";
import { Message } from "@/model/FcmMessage";
import { AdminService } from "@/services/AdminService";

export default defineComponent({
  data() {
    const message: { topic?: string; title?: string; description?: string } =
      {};
    const topics: string[] = [];
    return { message, topics, selectedTopic: topics[0] };
  },
  async mounted() {
    this.topics = await new AdminService().getAllTopics();
  },
  methods: {
    async sendPushNotification() {
      if (
        this.selectedTopic &&
        this.message.title &&
        this.message.description
      ) {
        const msg: Message = {
          topic: this.selectedTopic,
          webpush: {
            notification: {
              title: this.message.title,
              body: this.message.description,
              icon: "https://app.protezioneciviletricarico.com/img/icons/android-chrome-192x192.png",
              color: "#7e55c3",
              badge:
                "https://app.protezioneciviletricarico.com/img/icons/android-chrome-192x192.png",
              actions: [{ action: "goTo", title: "Prova" }],
            },
          },
        };
        await new NotificationService().sendNotification(msg);
      }
    },
  },
});
